    cursor: pointer;
    padding: 8px 30px;
    border-radius: 80px;
    box-shadow: 0 0 4px 0 rgb(31, 30, 30);
    transition: box-shadow 0.3s ease, padding 0.3s ease, border-radius 0.3s ease;
