.userProfile-container {
    background-color: rgba(255, 233, 241, 0.8);
    padding-bottom: 50px;
    height: auto;
}

.userProfile-subcontainer {
    position: relative;
}

.userProfile-img {
    width: 100%;
    border-radius: 3px;
}

.userProfile-info-container {
    display: block;
    position: absolute;
    bottom: 1em;
    left: 1em;
    color: white;
    font-weight: 900;
}

.userProfile-info-1 {
    display: flex;
    justify-content: space-around;
    height: 40px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: 0;
    padding: 5px;
    background-color: rgba(255, 172, 201, 0.8);
    border-radius: 5px;

}

.userProfile-name {
    font-size: 18px;
    margin-top: 2px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.userProfile-age {
    margin-left: 10px;
    font-size: 16px;
    margin-top: 5px;
 
    width: -webkit-fit-content;
 
    width: -moz-fit-content;
 
    width: fit-content;
}

.userProfile-bio {
    
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 5px;
    height: 25px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: 5px 0;
    background-color: rgba(255, 172, 201, 0.8);
    border-radius: 5px;
}

.userProfile-dates-home {
    display: flex;
    justify-content: center;
    margin-top: 10px;
}

.userProfile-date-title {
    font-weight: 900;
    display: flex;
    justify-content: center;
    /* margin-top: 10px; */
    padding-top: 20px;
    width: 40%;
    text-align: center;
    font-size: 18px;
}

.userProfile-link-container {
    display: flex;
    justify-content: space-between;
    margin-right: 20px;
    margin-top: 10px;
    margin-left: 20px;
}

.userProfile-link {
    text-decoration: none;
    color: black;
    border: 2px solid rgba(255, 172, 201, 0.8);
    width: 100px;
    border-radius: 15px;
    font-size: 14px;
    font-weight: 900;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: white;
}

.userProfile-link:hover {
    background-color: rgba(255, 172, 201, 0.8);
    color: black;
    border: 2px solid rgb(255, 248, 248);
    padding: 2px;
}

.userProfile-container-date {
    height: auto;
    margin: 15px;

}



.userProfile-detail-date {
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 2px solid rgba(255, 172, 201, 0.8);
    width: 90%;
    margin: 10px 0;
    border-radius: 15px;
    background-color: white;
    font-size: 14px;
    margin: 0 auto;
    padding: 10px;
}

.userProfile-detail-date-name {
    margin: 0;
    text-align: center;
    font-weight: 900;
    margin-top: 5px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    font-size: 18px;
}

.userProfile-detail-date-description {
    margin: 0;
    text-align: center;
}


.userProfile-button {
    display: flex;
    justify-content: flex-end;
    width: 100%;
}

.userProfile-date-button {
    margin: 5px;
    width: 60px;
    border: 2px solid rgba(255, 172, 201, 0.8);
    background-color: white;
    border-radius: 15px;
    margin: 5px;
    font-weight: 900;
    font-size: 11px;
    padding: 1px;
}

.userProfile-date-button:hover {
    background-color: rgba(255, 172, 201, 0.8);
    color: black;
    border: 2px solid rgb(255, 248, 248);
  
}

.userProfile-detail-date-category-content {
    margin: 0;
    width: 100%;
    display: flex;
    justify-content: flex-end;
    margin-top: 10px;
}


.userProfile-button-subcontainer {
    display: flex;
    justify-content: center;
}

.userProfile-button-create {
    font-size: 14px;
    background-color: white;
    border-radius: 15px;
    border: 2px solid rgba(255, 172, 201, 0.8);
    width: 120px;
    font-weight: 900;
    padding: 1px;
}

.userProfile-button-create:hover {
    background-color: rgba(255, 172, 201, 0.8);
    color: black;
    border: 2px solid rgb(255, 248, 248);
    
}

.userProfile-modal-title {
    color: black(255, 172, 201, 0.8)
}

.userProfile-modal-title {
    text-transform: uppercase;
    display: flex;
    justify-content: flex-start;
